<template>
  <div class="card-box">
    <div class="card-header">
      <!--  -->
      <span style="display:flex; width: 7px; height: 30px; background: #409EFF; margin-top: 65px"></span>
      <h4 style="margin-left:20px;font-size:19px"></h4>
    </div>
    <div class="card-container">
      <!--  -->
      <div class="card-false" v-if="this.flag === false" style="margin-top:-30px">
        <img src="../assets/images/loss-0.svg" alt style="width: 70px; heigth: 70px" />
        <p style="margin-top:-120px">
          
          <span style="color:rgb(9, 142, 75); font-weight: 600; font-size: 17px"></span>
        </p>
      </div>
      <div class="flag-true" style="margin-top:-30px" v-if="this.flag === true">
        <img src="../assets/images/loss-1.svg" alt style="width:80px; heigth:80px" />
        <p style="margin-top:-120px">
          
          <span style="color:rgb(255, 128, 0); font-weight: 600; font-size: 17px"></span>
        </p>
      </div>
    </div>
    <!--  -->
    <div class="card-input">
      <el-form
        :model="ruleForm"
        status-icon
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item
          label=""
          prop="username"
          style="width:370px"
          :rules="[{ requires: true,message: '' }]"
        >
          <el-input
            type="eleusername"
            v-model="ruleForm.eleusername"
            autocomplete="off"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" style="width:370px">
          <el-input
            type="password"
            v-model="ruleForm.elepassword"
            autocomplete="off"
            placeholder=""
            clearable
            :rules="[{ required: true, message:'' }]"
          ></el-input>
        </el-form-item>
      </el-form>
    </div>
    <!--  -->
    <div class="findcard" style="margin-top: -20px">
      <div class="true-footer" v-if="this.flag === false">
        <el-button
          type="danger"
          style="width: 350px; height: 40px; font-size: 14px; margin-left: 25px;"
          @click="LossorFind('ruleForm')"
        ></el-button>
      </div>
      <div class="true-footer" v-if="this.flag === true">
        <el-button
          type="success"
          style="width:350px; height: 40px; font-size: 14px; margin-left: 25px;"
          @click="LossorFind('ruleForm')"
        ></el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'CardLoss',
  data () {
    return {
      ruleForm: {
        eleusername: '',
        elepassword: ''
      },
      flag: '',
    }
  },
  mounted () {
    this.judgeFlag()
    this.LossorFind()
  },
  methods: {
    // flagor
    async judgeFlag () {
      await this.axios({
        method: 'get',
        url: 'https://www.mzkt.xyz/smart-school/electricity/queryLostFlag',
      }).then(res => {
        this.flag = res.data.flag
        console.log('flag' + res.data.flag)
      }).catch(err => {
        console.log(err)
      })
    },
    // or
    async LossorFind () {
      await this.axios({
        method: 'get',
        url: 'https://www.mzkt.xyz/smart-school/electricity/lostCard',
        params: {
          account: this.ruleForm.eleusername,
          password: this.ruleForm.elepassword,
          flag: this.flag
        }
      }).then(res => {
        console.log(res)
        if (res.data.code === '200') {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'success'
          })
          this.judgeFlag()
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style>
.card-header {
  display: flex;
  margin-top: -70px;
}

.el-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -20px;
}
</style>