<template>
  <el-container>
    <el-aside width="30%">
      <!--  -->
      <div class="left-container">
        <div class="title">
          <p style=" color: #fff;"></p>
          <p style=" color: #fff;"></p>
        </div>
        <img
          src="../assets/images/bgc-login.svg"
          alt
          width="70%"
          height="250px"
          style="margin-top: 15px"
        />
      </div>
    </el-aside>
    <!--  -->
    <el-main>
      <div class="right-container">
        <div class="right-title" style="line-height: 20px; margin-bottom: 5px">
          <h2>Hello</h2>
          <p style="margin-top: 5px">
            
            <span style="color: #439AF8"></span>
          </p>
        </div>
        <el-form :model="ruleForm" status-icon ref="ruleForm" class="demo-ruleForm">
          <el-form-item
            style="margin-bottom: -1px"
            prop="username"
            :rules="[
                { required: true,message:'' }
              ]"
          >
            <el-input
              type="username"
              v-model="ruleForm.username"
              prefix-icon="el-icon-user"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item
            prop="password"
            :rules="[
                {required: true,message:''}
              ]"
          >
            <el-input
              type="password"
              v-model="ruleForm.password"
              prefix-icon="el-icon-lock"
              placeholder=""
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              @click="submitForm('ruleForm')"
              style=" width:320px; font-size: 14px"
            > </el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      ruleForm: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    async login () {
      const loading = this.$loading({
        lock: true,
        text: '...',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      await this.axios({
        method: 'get',
        url: 'https://www.mzkt.xyz:443/smart-school/education/login',
        params: {
          username: this.ruleForm.username,
          password: this.ruleForm.password
        }
      }).then(res => {
        const code = res.data.code
        if (code === '200') {
          localStorage.setItem('saveUsername', this.ruleForm.username)
          localStorage.setItem('savePassword', this.ruleForm.password)
          localStorage.setItem('adminFlag', res.data.flag)
          //token
          localStorage.setItem('eaToken', res.data.token)
          this.isLogin()
          setTimeout(() => {
            loading.close()
            this.$router.push('home')
            this.$message({
              message: '',
              type: 'success'
            });
          }, 1200)

        } else if (code === '202') {
          loading.close()
          this.$alert('', '', {
            confirmButtonText: ''
          })
        } else if (code === '404') {
          loading.close()
          this.$alert('', '', {
            confirmButtonText: ''
          })
        }
      })
    },
    async isLogin () {
      await this.axios({
        method: 'get',
        url: 'https://www.mzkt.xyz/smart-school/education/toLogin'
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.login()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  }
}
</script>

<style lang="less" scoped>
.el-aside {
  background-color: #567bff;
  color: #fff;
  text-align: center;
  .left-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .title p {
    padding-bottom: 15px;
    font-size: 18px;
  }
}

.el-main ::v-deep .el-input {
  width: 320px;
  margin-bottom: 3px;
}

.el-main ::v-deep .el-input__inner {
  height: 40px;
  line-height: 41px;
}

.el-main ::v-deep .el-input__icon {
  line-height: 40px;
  font-size: 14px;
}

.el-main {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  .right-container {
    text-align: start;
    vertical-align: middle;
  }
}

.el-button {
  height: 40px;
}
</style>
