<template>
  <div class="SchoolInvest-box">
    <div class="ScInvest-header">
      <!--  -->
      <span style="display:flex; width: 7px; height: 30px; background: #409EFF; margin-top: 65px"></span>
      <h4 style="margin-left:20px; font-size:19px"></h4>
    </div>
    <div class="ScInvest-container">
      <!--  -->
      <div class="Invest-amount" style="margin-top: -45px">
        <h3 style="font-size: 17px">:</h3>
        <el-row v-model="changeValue" style="margin-top: 5px" type="flex">
          <span
            v-for="(items, index) in buttonValue"
            :key="index"
            class="btn-checked"
            :class="buttonIndex === index ? 'active' : ''"
            @click="onSubmit(index)"
          >{{ items.value }} </span>
          <span class="pay-diy">
            <input type="text" placeholder="" v-model="inputMoney" />
          </span>
        </el-row>
      </div>
      <!--  -->
      <div class="Invest-method" style="margin-top:20px">
        <h3 style="font-size: 17px"></h3>
        <el-select v-model="value" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.roomNum"
          ></el-option>
        </el-select>
      </div>
      <!--  -->
      <div class="Invest-chaxun" style="margin-top:20px">
        <h3 style="font-size: 17px"></h3>
        <el-input
          type="password"
          placeholder=""
          v-model="elePassword"
          show-password
          clearable
          style="width:215px"
        ></el-input>
      </div>
    </div>
    <el-row class="btn-pay">
      <el-button type="primary" style="width:300px; margin-top:60px" @click="deposit()"></el-button>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Inquire',
  data () {
    return {
      options: [{
        value: '1',
        label: ''
      }],
      elePassword: '',
      buttonIndex: 8,
      inputMoney: '',
      value: '',
      // value
      changeValue: '',
      // 
      buttonValue: [
        { value: '10' },
        { value: '20' },
        { value: '30' },
        { value: '50' },
        { value: '100' },
        { value: '200' },
        { value: '300' }
      ]
    }
  },
  created () {
    this.getLoginAccount()
  },
  methods: {
    // 
    getLoginAccount () {
      this.loginAccount = localStorage.getItem('el-account')
    },
    async deposit () {
      var Base64 = require('js-base64').Base64
      var inputpassword = Base64.encode(this.elePassword);
      await this.axios({
        method: 'post',
        url: 'https://www.mzkt.xyz/smart-school/electricity/recharge',
        params: {
          account: this.loginAccount,
          password: inputpassword,
          tranamt: this.changeValue || this.inputMoney
        }
      }).then(res => {
        if (res.data.code === '200') {
          this.$router.push('/home/refresh')
          setTimeout(() => {
            this.$alert('', '', {
              confirmButtonText: ''
            })
          }, 1500)
        } else if (res.data.code === '202') {
          this.$alert('', '', {
            confirmButtonText: ''
          })
        }
      })
    },
    // 
    onSubmit (index) {
      this.buttonIndex = index
      this.changeValue = this.buttonValue[index].value
      console.log('value' + this.changeValue)
    }
  }
}
</script>

<style lang="less" scoped>
.ScInvest-header {
  display: flex;
  margin-top: -70px;
}

.ScInvest-container {
  width: 100%;
  line-height: 40px;
  text-align: start;
}

.Invest-amount .el-button {
  width: 80px;
  height: 30px;
  margin-right: 10px;
}

.btn-pay .el-button {
  height: 40px;
  font-size: 14px;
}

.btn-checked {
  display: block;
  width: 105px;
  height: 45px;
  line-height: 45px;
  background-color: #ecf5ff;
  margin-right: 15px;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #b3d8ff;
  color: #409eff;
  transition: 0.2s;
  cursor: pointer;
}

.btn-checked:hover {
  background-color: #409eff;
  color: #ffffff;
  cursor: pointer;
}

.active {
  background-color: #409eff;
  color: #fff;
}

.pay-diy {
  display: block;
  width: 105px;
  height: 45px;
  line-height: 45px;
  background-color: #ecf5ff;
  margin-right: 15px;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #b3d8ff;
  color: #409eff;
  transition: 0.2s;
  cursor: pointer;
}

.pay-diy input {
  width: 95px;
  height: 40px;
  border: 0;
  font-size: 15px;
  text-align: center;
  background-color: #ecf5ff;
  outline: none;
}

.pay-diy input::-webkit-input-placeholder {
  color: #409eff;
  text-align: center;
}
</style>
