<template>
  <div class="inquire-box">
    <div class="inquire-header">
      <el-row type="flex" align="center">
        <span style="display:flex; width: 7px; height:30px; background:#409EFF;margin-top:65px"></span>
        <span style="margin-left:20px;font-size:19px; font-weight: 600"></span>
      </el-row>
      <!--  -->
      <div class="inquire-block">
        <el-date-picker
          v-model="valuedate"
          type="daterange"
          align="right"
          unlink-panels
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions"
          format="yyyy  MM  dd "
          value-format="yyyy-MM-dd"
        ></el-date-picker>
        <el-button type="primary" style="margin-left:8px;" @click="flowInform('rows')"></el-button>
      </div>
    </div>
    <!--  -->
    <vue-scroll :ops="ops" style="overflow-y: auto; height: 680px">
      <div class="inquire-container">
        <el-table :v-for="rows" :data="rows" style="width: 99%" class="box-container" stripe>
          <el-table-column type="index" label="" align="center"></el-table-column>
          <el-table-column prop="OCCTIME" label="" align="center"></el-table-column>
          <el-table-column prop="MERCNAME" label="" align="center"></el-table-column>
          <el-table-column prop="POSCODE" label="POS" align="center"></el-table-column>
          <el-table-column prop="TRANAMT" label="" align="center"></el-table-column>
          <el-table-column prop="TRANNAME" label="" align="center"></el-table-column>
          <el-table-column prop="CARDBAL" label="" align="center"></el-table-column>
        </el-table>
      </div>
    </vue-scroll>
  </div>
</template>

<script>
export default {
  name: 'Inquire',
  data () {
    return {
      // 
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick (picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      // 
      ops: {
        bar: {
          hoverStyle: true,
          onlyShowBarScroll: false,
          background: "#8A8A8A",
          opacity: 0.3
        }
      },
      value: new Date(),
      valuedate: [],
      pickdata: {
        startTime: '',
        endTime: ''
      },
      search: {
        date: [],
        beginDate: '',
        endDate: ''
      },
      rows: [],
    }
  },
  created () {
    this.getLoginAccount()
    this.search.beginDate = this.handleTimeOld(new Date())
    this.search.endDate = this.handleTimeNew(new Date())
    this.search.date.push(this.search.beginDate)
    this.search.date.push(this.search.endDate)
    this.valuedate[0] = this.search.date[0]
    this.valuedate[1] = this.search.date[1]
  },

  mounted () {
    this.flowInform()
  },
  methods: {
    // 
    getLoginAccount () {
      this.loginAccount = localStorage.getItem('el-account')
    },
    formatNumber (number) {
      return String(number)[1] ? String(number) : `0${number}`
    },
    handleTimeOld (time, split) {
      let date = new Date(time)
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      split = '-'
      return [year, month, 1].map(num => this.formatNumber(num)).join(split)
    },
    handleTimeNew (time) {
      let date = new Date(time)
      let year = date.getFullYear()
      let month = (date.getMonth() + 1) > 10 ? (date.getMonth() + 1) : ('0' + (date.getMonth() + 1))
      let day = date.getDate() > 10 ? date.getDate() : ('0' + date.getDate())
      return `${year}-${month}-${day}`
    },
    // 
    async flowInform () {
      this.pickdata.startTime = this.valuedate[0]
      this.pickdata.endTime = this.valuedate[1]
      await this.axios({
        method: 'get',
        url: 'https://www.mzkt.xyz/smart-school/electricity/queryFlow',
        params: {
          account: this.loginAccount,
          startDate: this.pickdata.startTime,
          endDate: this.pickdata.endTime
        }
      }).then(res => {
        if (res.data.code === '200') {
          console.log('')
          console.log(res.data.rows)
          this.rows = res.data.rows
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // 
    indexMethod (index) {
      return index + 1;
    }
  }
}
</script>

<style lang="less" scoped>
.inquire-header {
  display: flex;
  justify-content: space-between;
  margin-top: -65px;
}

.box-container {
  line-height: 20px !important;
  color: black;
  height: 100%;
}

.el-table {
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
}
</style>